
import {
  defineComponent,
  ref,
  computed,
  watch,
  onBeforeMount,
  onMounted,
  onBeforeUpdate,
  onUpdated,
  onBeforeUnmount,
  onUnmounted,
  onErrorCaptured,
  inject,
} from "vue";
import useGetList from "./useGetList";
export default defineComponent({
  name: "HelloWorld",
  props: {
    msg: String,
  },
  emits: ["change"],
  setup(props, ctx) {
    console.log(props, ctx);

    const homeData = inject("homeData");
    const changeHomeData = inject("changeHomeData");
    let clicked = ref(0);
    const changeHomeDataFromChild = () => {
      if (typeof changeHomeData === "function") {
        clicked.value = clicked.value + 1;
        changeHomeData(`from helloWord ! clicked: ${clicked.value}`);
      }
    };

    let count = ref(0);
    const increme = () => {
      count.value++;
      ctx.emit("change", count.value);
    };

    const doubleCount = computed(() => {
      return count.value * 2;
    });

    watch(doubleCount, (newValue, oldValue) => {
      console.log("newValue", newValue);
      console.log("oldValue", oldValue);
    });

    const { loading, list, getList, setList } = useGetList();

    onBeforeMount(() => {
      console.log("onBeforeMount");
    });

    onMounted(() => {
      console.log("onMounted");
    });

    onBeforeUpdate(() => {
      console.log("onBeforeUpdate");
    });

    onUpdated(() => {
      console.log("onUpdated");
    });

    onBeforeUnmount(() => {
      console.log("onBeforeUnmount");
    });

    onUnmounted(() => {
      console.log("onUnmounted");
    });

    onErrorCaptured(() => {
      console.log("onErrorCaptured");
    });

    onMounted(() => {
      document.getElementById("ul")?.addEventListener("click", (e) => {
        console.log(e.target, e.currentTarget);
      });
    });

    return {
      homeData,
      changeHomeData,
      changeHomeDataFromChild,

      count,
      doubleCount,
      increme,

      loading,
      list,
      getList,
      setList,
    };
  },
});
